@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');




.nav{
  font-family: 'Bebas Neue', cursive;
  font-weight: bold;
  font-size: 25px;
  
}
.menudown:hover{
  background-color: #df4759;

}
 
 .button{
   margin-top: auto;
  font-family: 'Montserrat', sans-serif;
  font-weight: bolder;
  font-size: large;
 }


.logo{
  height: 5rem;
  
}

.products{
  font-family: 'Montserrat', sans-serif;
  font-weight:inherit;
  background-color: #F2C029;
  padding: 0px;
  
}

.card_titles{
  font-family: 'Bebas Neue', cursive;
  font-weight: bold;
  text-shadow: 0px 0px 5px #260C07;
  
}

.titles_shop{
  font-family: 'Bebas Neue', cursive;
  font-weight: bold;
  font-size: 4rem;
  text-shadow: 5px 0px 2px #F2C029;
  letter-spacing: 0.5rem;
  
}


.image{

max-height: 15rem;
}


a{
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  font-family: 'Bebas Neue', cursive;
  
}
.conh1{
  justify-content: center;
  height: fit-content;
  width: auto;
  background-color: #F2C029;
  border-radius: 10px;
  margin-right: auto;
}

.conh3{
  background-color: #df4759 ;
  border-radius: 10px;
  padding: 10px;
  
}

h1 {
  color: #260C07;
  text-shadow: 0px 3px 5px #F2C029;
  font-weight: bold;
  font-family: 'Bebas Neue', cursive;
  
  
  
}
.text{
  font-family: 'Montserrat', sans-serif;
}
h3{
  color: #F2C029;
  
  font-weight: bold;
  font-family: 'Montserrat', sans-serif;
  
  
}
.descrip{
  font-weight: bold;
  font-family: 'Montserrat', sans-serif;
}

.indexdesc {
  color: #F2C029;

  font-size: 30px;
  font-weight: italic;
  font-family: 'Bebas Neue', cursive;
}


.cont {
  display: flex;
