  font-family: 'Montserrat', sans-serif;
 
}

.about {
  font-size: 20px;
}
.sobre {
  padding: 10px;
  border-radius: 10px;
  
  border-right: medium;
  border-color: #260C07;
  border-width: medium;
}
.title{
  font-family: 'Bebas Neue', cursive;
  
}
